.wrapper {
  width: 100%;
  max-width: 820px;
  margin: 0 auto 4rem auto;
}

.header {
  text-align: center;
}

.title,
.subtitle {
  margin: 0;
}

.title {
  color: inherit;
  text-decoration: none;
}

.subtitle {
  color: var(--color-text-light);
  font-weight: var(--weight-bold);
}
